export default {
  name: 'MainContent',
  data() {
    return {
      vueRecommend: [
        {
          siteIcon: require("@/assets/img/weapp/wepy.png"),
          siteTitle: 'WePY',
          siteDescription: '让小程序支持组件化开发的框架',
          siteUrl: "https://wepyjs.github.io/wepy-docs/"
        },
        {
          siteIcon: require("@/assets/img/weapp/mpvue.png"),
          siteTitle: 'mpvue',
          siteDescription: 'mpvue 是一个使用 Vue.js 开发小程序的前端框架',
          siteUrl: "http://mpvue.com"
        },
        {
          siteIcon: require("@/assets/img/vue/vant.png"),
          siteTitle: 'Vant Weapp',
          siteDescription: '轻量、可靠的小程序 UI 组件库',
          siteUrl: "https://youzan.github.io/vant-weapp/#/intro"
        },
        {
          siteIcon: require("@/assets/img/weapp/kbone.png"),
          siteTitle: 'kbone',
          siteDescription: 'kbone 是一个致力于微信小程序和 Web 端同构的解决方案',
          siteUrl: "https://developers.weixin.qq.com/miniprogram/dev/extended/kbone/"
        },
        {
          siteIcon: require("@/assets/img/weapp/uniapp.png"),
          siteTitle: 'uni-app',
          siteDescription: 'uni-app 是一个使用 Vue.js 开发小程序、H5、App的统一前端框架',
          siteUrl: "https://uniapp.dcloud.io"
        },
        {
          siteIcon: require("@/assets/img/weapp/omi.svg"),
          siteTitle: 'Omi',
          siteDescription: 'Omi - 前端跨框架跨平台框架，基于 Web Components 并支持 IE8+(omio)，小程序(omi-kbone) 和 任意前端框架集成',
          siteUrl: "https://tencent.github.io/omi/"
        },
        {
          siteIcon: require("@/assets/img/weapp/mpx.png"),
          siteTitle: 'MPX',
          siteDescription: 'Mpx是一款致力于提高小程序开发体验的增强型小程序框架，通过Mpx，我们能够以最先进的web开发体验来开发生产性能深度优化的小程序',
          siteUrl: "https://didi.github.io/mpx/"
        },
        {
          siteIcon: require("@/assets/img/weapp/megalo.png"),
          siteTitle: 'Megalo',
          siteDescription: '基于 Vue 的小程序框架，支持微信小程序、支付宝小程序、百度智能小程序',
          siteUrl: "https://megalojs.org/#/"
        },
        {
          siteIcon: require("@/assets/img/vue/iview.png"),
          siteTitle: 'iView',
          siteDescription: '一套高质量的微信小程序 UI 组件库',
          siteUrl: "https://weapp.iviewui.com/"
        },
        {
          siteIcon: require("@/assets/img/weapp/wux.png"),
          siteTitle: 'Wux Weapp',
          siteDescription: '一套组件化、可复用、易扩展的微信小程序 UI 组件库',
          siteUrl: "https://wuxui.com"
        },
        {
          siteIcon: require("@/assets/img/nav_menu/github.png"),
          siteTitle: 'awesome-wechat-weapp',
          siteDescription: '微信小程序开发资源汇总',
          siteUrl: "https://github.com/justjavac/awesome-wechat-weapp"
        },
      ]
    }
  },
}
</script>

<style>
a {
  text-decoration: none;
  color: #373e4a;
}

.card-icon {
  height: 40px;
  width: 40px;
  float: left;
  margin-right: 10px;
}

.box-card {
  border-radius: 12px;
  height: 90px;
  margin-top: 15px;
  /* display: flex; */
}

.site-title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.site-description {
  color: #979898;
  font-size: 13px;
  margin: 2px;
  word-break: break-word;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
</style>


